/*
React carousel component
*/

//import Slider from 'react-slick';
import {React_Bootstrap_Carousel} from 'react-bootstrap-carousel';
import 'react-bootstrap-carousel/dist/bootstrap.min.css';
import 'react-bootstrap-carousel/dist/react-bootstrap-carousel.css';

export default class Carousel extends React.Component {
  constructor(props) {
    super(props);
  }

  onSelect(active, direction) {
    console.log(`active=${active} && direction=${direction}`);
  }

  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    var content = (
      <div className='carouselWrapper'>
        <React_Bootstrap_Carousel
            animation={true}
            onSelect={this.onSelect}
            className="carousel-fade"
          >
            <div id='slide1' className='carouselItem' style={{height:700,width:"100%",backgroundColor:"skyblue"}}>

            </div>
            <div id='slide2' className='carouselItem' style={{height:700,width:"100%",backgroundColor:"aqua"}}>

            </div>
            <div id='slide3' className='carouselItem' style={{height:700,width:"100%",backgroundColor:"lightpink"}}>

            </div>
          </React_Bootstrap_Carousel>
      </div>
    );

    return content;
  }
};



