/*
Footer component
*/

export default class Footer extends React.Component {
  constructor(props) {
    super(props);

    this.style = {};

    // this.state = {

    // };
  }

  render() {
    var content = (
      <div className="navbar navbar-fixed-bottom">
        <div className="footer-bottom transition-all">
          <div className="container-fluid">
            <hr/>
              {/* <p className="pull-left" id="cpyRight">Copyright &copy; 2017 Carl Flint. All rights reserved.</p> */}
              {/* Foot log pull left */}
              <ul className="list-inline pull-left" id="walkWico">
                <li>
                  <a href="#" target="_blank">
                    <i id="footLogo"/>
                  </a>
                </li>
              </ul>
              {/* Wico indian center */}
              <ul className="list-inline" id="wicomicoCrest">
                <li>
                  <a href="#" target="_blank">
                    <i id="wicoCrest"/>
                  </a>
                </li>
              </ul>
              {/* Social media pull right */}
              <ul className="list-inline pull-right" id="socialMedia">
                <li>
                  <a href="#" target="_blank">
                    <i className="fa fa-twitter fa-fw fa-3x"></i>
                  </a>
                </li>
                <li>
                  <a href="#" target="_blank">
                    <i className="fa fa-facebook fa-fw fa-3x"></i>
                  </a>
                </li>
                <li>
                  <a href="#" target="_blank">
                    <i className="fa fa-youtube fa-fw fa-3x"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      );

      return content;
    }
  };



