/*
home content component
*/
export default class Content extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {

    var content = (
      <div className='homeContent'>
        <section className="figure-nest row">
            <figure className="effect-move">
                <a href="/WiHD/#clubs">
                    <img id="placeHolder1"/>
                    <div className="floater text-vertical-center">
                        <h3>Walking Clubs</h3>
                    </div>
                </a>
            </figure>
            <figure className="effect-move">
                <a href="/WiHD/#map">
                    <img id="walkWicomicoLogo"/>
                    <div className="floater text-vertical-center">
                        <h3>View Trail Maps</h3>
                    </div>
                </a>
            </figure>
            <figure className="effect-move">
                <a href="/WiHD/#events">
                    <img id="placeHolder2"/>
                    <div className="floater text-vertical-center">
                        <h3>Events</h3>
                    </div>
                </a>
            </figure>
        </section>
      </div>
    );

    return content;
  }
};



