/*
React main component
*/

import Navbar from '../component/navbar.jsx';
//import TopNav from '../component/nav.jsx';
import Carousel from '../component/carousel.jsx';
import Content from '../component/main-body.jsx';
import ToTop from '../component/toTop.jsx';
import Footer from '../component/footer.jsx';

export default class Home extends React.Component {
  constructor(props) {
    super(props);

    this.style = {};

    // this.state = {

    // };
  }

  render() {
    var content = (
      <div>
        <Navbar/>
        {/* <h1 className='container'>Place holder for Homepage content</h1> */}
        <Carousel/>
        <Content/>
        <ToTop/>
        <Footer/>
        <div id="footerSpacer"></div>
      </div>
    );

    return content;
  }
};



