/*
App main file
*/
// import styles
import '../css/style.less';
import 'bootstrap';
// import custom scripts
import './scripts/toTop';
import './scripts/mobileNav';
// import backbone/react
import Router from './router/main';
import {HomeController, ClubsController, MapController, EventsController} from './controller/index.js';
import MainView from './view';


class Application {
  constructor() {
    //additional initialization
    this.initialize();
  }

  initialize() {
    let self = this;
    this._mainView = new MainView();
    // template for multiple pages
    // this._pageController = new PageController({
    //   app: self
    // });

    this._homeController = new HomeController({
      app: self
    });

    this._clubsController = new ClubsController({
      app: self
    });

    this._mapController = new MapController({
      app: self
    });

    this._eventsController = new EventsController({
      app: self
    });

    this._router = new Router({
      controllers: {
        // page: self._pageController,
        home: self._homeController,
        clubs: self._clubsController,
        map: self._mapController,
        events: self._eventsController
      }
    });
    //start backbone history
    Backbone.history.start();
    console.log('app finished initializing!');
  }

  get router() {
    return this._router;
  }
}


//start up function
$(() => {
  //webpack works
  console.log('Loading...');

  let app = new Application();
  // console.log(app);
});



